import { SDKVariableStorage } from '../variables/VariableStorage';

import { ThirdPartyToken } from './ThirdPartyToken';

const THIRD_PARTY_TOKEN_CACHE_KEY = 'sdkThirdPartyToken';

export const getCachedThirdPartyToken = (): ThirdPartyToken | null => {
  if (!SDKVariableStorage.CACHE_STORAGE) {
    return null;
  }

  return SDKVariableStorage.CACHE_STORAGE.getItem(THIRD_PARTY_TOKEN_CACHE_KEY);
};

export const cacheThirdPartyToken = (token: ThirdPartyToken): void => {
  if (SDKVariableStorage.CACHE_STORAGE) {
    SDKVariableStorage.CACHE_STORAGE.setItem(
      THIRD_PARTY_TOKEN_CACHE_KEY,
      token,
      token.expires_in * 1000,
    );
  }
};

export const clearCachedThirdPartyToken = (): void => {
  if (!SDKVariableStorage.CACHE_STORAGE) {
    return;
  }

  SDKVariableStorage.CACHE_STORAGE.removeItem(THIRD_PARTY_TOKEN_CACHE_KEY);
};
