import { ChatSDKError } from '../error/ChatSDKError';

export enum EnvironmentName {
  AU1 = 'AU1',
  CA1 = 'CA1',
  EU1 = 'EU1',
  JP1 = 'JP1',
  NA1 = 'NA1',
  UK1 = 'UK1',
  custom = 'custom',
}

export interface EnvironmentEndpoints {
  authorize: string;
  chat: string;
  gateway: string;
  name: string;
}

export function buildEnvironmentEndpoints(
  environment: EnvironmentName,
): EnvironmentEndpoints {
  if (!(environment in EnvironmentName)) {
    throw new ChatSDKError(`Unknown environment: ${environment}`);
  }

  if (environment === EnvironmentName.custom) {
    throw new ChatSDKError(
      'The custom environment cannot be constructed using the built-in configuration',
    );
  }

  const chat =
    `https://channels-de-${environment}.niceincontact.com`.toLowerCase();
  const gateway =
    `wss://chat-gateway-de-${environment}.niceincontact.com`.toLowerCase();
  const name = environment;
  const authorize =
    `https://digital-oauth-de-${environment}.niceincontact.com`.toLowerCase();

  return {
    chat,
    name,
    gateway,
    authorize,
  };
}
