import { ChannelInfo } from '@brandembassy/ts-types-chat/dist/channel/ChannelInfo';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';

import { getBrowserLanguage } from '../browserFingerprint/getBrowserFingerprint';
import { ChatSDKError } from '../error/ChatSDKError';
import { SdkVersionNotSupported } from '../error/SdkVersionNotSupported';
import { isSdkVersionNotSupportedEvent } from '../event/middleware/sdkVersionCheck';
import { defaultHeaders } from '../request/defaultHeaders';
import { getErrorResponseData } from '../request/getErrorResponseData';
import { SDKVariableStorage } from '../variables/VariableStorage';

export async function fetchChannelInfo(
  brandId: BrandId,
  channelId: ChannelId,
  language = getBrowserLanguage(),
): Promise<ChannelInfo> {
  const languageLocale = `${language}`.split('-').join('_');
  const CHANNEL_INTEGRATION_URL = SDKVariableStorage.ENDPOINT_CHAT;

  const response = await fetch(
    `${CHANNEL_INTEGRATION_URL}/chat/1.0/brand/${brandId}/channel/${channelId}?locale=${languageLocale}`,
    {
      method: 'GET',
      headers: {
        ...defaultHeaders,
      },
    },
  );

  if (!response.ok) {
    const errorData = await getErrorResponseData(response);

    if (isSdkVersionNotSupportedEvent(errorData)) {
      throw new SdkVersionNotSupported();
    }

    throw new ChatSDKError(
      `Failed to fetch channel info. Status (${response.status})`,
      { response },
    );
  }

  return response.json();
}
