import { SDKVariableStorage } from '../variables/VariableStorage';

import { EnvironmentEndpoints } from './Environments';

export const setEnvironmentEndpoints = (
  endpoints: EnvironmentEndpoints,
): void => {
  SDKVariableStorage.ENDPOINT_GATEWAY = endpoints.gateway;
  SDKVariableStorage.ENDPOINT_CHAT = endpoints.chat;
  SDKVariableStorage.ENDPOINT_AUTHORIZE = endpoints.authorize;
};
