import { ChatSDKError } from '../error/ChatSDKError';
import { ERROR_CANNOT_REFRESH_THIRD_PARTY_TOKEN } from '../error/errorMessages';
import { postJSON } from '../utils/fetchJSON';
import { isNull } from '../utils/is';

import { getAuthorizeUrl } from './getAuthorizeUrl';
import { handleNewThirdPartyToken } from './handleNewThirdPartyToken';
import { getThirdPartyToken } from './thirdPartyTokenStorage';
import { RefreshTokenResponse } from './TransactionToken';

export async function refreshThirdPartyToken(): Promise<void> {
  const token = getThirdPartyToken();
  if (isNull(token)) {
    throw new ChatSDKError(ERROR_CANNOT_REFRESH_THIRD_PARTY_TOKEN);
  }

  const refreshUrl = getAuthorizeUrl();
  try {
    const { thirdParty } = await postJSON<RefreshTokenResponse>(
      refreshUrl,
      {
        thirdParty: {
          grant_type: 'refresh_token',
          refresh_token: token.refresh_token,
        },
      },
      {
        credentials: 'include',
      },
    );

    if (!thirdParty) {
      throw new ChatSDKError(ERROR_CANNOT_REFRESH_THIRD_PARTY_TOKEN);
    }

    return handleNewThirdPartyToken(thirdParty);
  } catch (error) {
    if (error instanceof ChatSDKError) {
      throw error;
    }

    throw new ChatSDKError(ERROR_CANNOT_REFRESH_THIRD_PARTY_TOKEN, error);
  }
}
