import { CustomField } from '@brandembassy/ts-types-platform/dist/customField/CustomField';
import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/endUser/EndUserIdentityId';

import {
  CustomFieldsMap,
  CustomFieldsObject,
  getCustomFields,
  getCustomFieldsArray,
  setCustomFields,
  setCustomFieldsFromArray,
} from '../customField/CustomFields';
import { ChatEventData } from '../event/ChatEvent';
import { sendChatEvent } from '../event/sendChatEvent';
import { CustomerIdentity } from '../securedSession/TransactionToken';
import { WebSocketClient } from '../sockets/WebSocketClient';
import { generateId } from '../utils/generateId';
import { isArray } from '../utils/is';

import { createSetCustomerCustomFieldsPayloadData } from './setCustomerCustomFieldsPayload';

let CUSTOMER_ID: CustomerIdentityIdOnExternalPlatform | undefined;
let CUSTOMER_NAME: string | undefined;
let CUSTOMER_IMAGE: string | undefined;
let CUSTOMER_TEMP_CUSTOM_FIELDS: Array<CustomField> | undefined;

export function presetCustomerIdentity(
  customerIdentity: CustomerIdentity,
): void {
  const {
    idOnExternalPlatform,
    firstName = '',
    lastName = '',
    image,
    customFields,
  } = customerIdentity;
  CUSTOMER_ID = idOnExternalPlatform;

  if (firstName || lastName) {
    CUSTOMER_NAME = `${firstName ?? ''} ${lastName ?? ''}`.trim();
  }

  if (image) {
    CUSTOMER_IMAGE = image;
  }

  if (isArray(customFields)) {
    CUSTOMER_TEMP_CUSTOM_FIELDS = customFields;
  }
}

export class Customer {
  protected _websocketClient: WebSocketClient | null;
  protected _customFields: CustomFieldsMap = new Map();
  protected _exists = false;

  constructor(
    id: CustomerIdentityIdOnExternalPlatform,
    name: string | undefined,
    image: string | undefined,
    websocketClient: WebSocketClient | null,
  ) {
    this._websocketClient = websocketClient;
    Customer.setId(id);
    Customer.setName(name);
    Customer.setImage(image);

    if (isArray(CUSTOMER_TEMP_CUSTOM_FIELDS)) {
      this.setCustomFieldsFromArray(CUSTOMER_TEMP_CUSTOM_FIELDS);
      CUSTOMER_TEMP_CUSTOM_FIELDS = undefined;
    }
  }

  static setId(id: CustomerIdentityIdOnExternalPlatform): void {
    CUSTOMER_ID = id;
  }

  static getId(): CustomerIdentityIdOnExternalPlatform | null {
    return CUSTOMER_ID ?? null;
  }

  static getName(): string | undefined {
    return CUSTOMER_NAME;
  }

  static setName(name?: string): void {
    CUSTOMER_NAME = name;
  }

  static getIdOrCreateNewOne(): CustomerIdentityIdOnExternalPlatform {
    let customerId = this.getId();

    if (!customerId) {
      customerId = generateId();
      this.setId(customerId);
    }

    return customerId;
  }

  static getImage(): string | undefined {
    return CUSTOMER_IMAGE;
  }

  static setImage(image?: string): void {
    CUSTOMER_IMAGE = image;
  }

  static destroy(): void {
    CUSTOMER_ID = undefined;
    CUSTOMER_NAME = undefined;
    CUSTOMER_IMAGE = undefined;
    CUSTOMER_TEMP_CUSTOM_FIELDS = undefined;
  }

  public getId(): CustomerIdentityIdOnExternalPlatform {
    return Customer.getIdOrCreateNewOne();
  }

  public getName(): string | undefined {
    return Customer.getName();
  }

  public setName(name?: string): void {
    Customer.setName(name);
  }

  public setImage(image?: string): void {
    Customer.setImage(image);
  }

  public setExists(exists: boolean): void {
    this._exists = exists;
  }

  /**
   * Set Customer Custom field
   * @param name - Custom field name
   * @param value - Custom field value
   */
  public setCustomField(
    name: CustomField['ident'],
    value: CustomField['value'],
  ): Promise<ChatEventData> | undefined {
    return this.setCustomFields({ [name]: value });
  }

  /**
   * Set Customer Custom fields
   * @param customFields - custom fields object
   * @example setCustomFields(\{ identName: 'value', identName2: 'value2' \})
   */
  public setCustomFields(
    customFields: CustomFieldsObject,
  ): Promise<ChatEventData> | undefined {
    setCustomFields(this._customFields, customFields);

    if (this._exists) {
      return this.sendCustomFields();
    }
  }

  /**
   * Get Customer Custom fields (as object)
   */
  public getCustomFields(): CustomFieldsObject {
    return getCustomFields(this._customFields);
  }

  /**
   * Set Customer Custom fields from an array of custom fields
   * @param customFields - custom fields array
   */
  public setCustomFieldsFromArray(customFields: Array<CustomField>): void {
    setCustomFieldsFromArray(this._customFields, customFields);
  }

  /**
   * Get Customer Custom fields (as array of CustomField)
   */
  public getCustomFieldsArray(): Array<CustomField> {
    return getCustomFieldsArray(this._customFields);
  }

  /**
   * Send Customer Custom fields
   * - call this only after the first message or recover event
   * @returns Promise<ChatEventData>
   */
  public async sendCustomFields(): Promise<ChatEventData> {
    const payloadData = createSetCustomerCustomFieldsPayloadData(
      getCustomFieldsArray(this._customFields),
    );

    return sendChatEvent(payloadData, this._websocketClient);
  }
}
