import { ChatSDKError } from '../error/ChatSDKError';
import { ChatEventData } from '../event/ChatEvent';
import { normalizeEvent } from '../event/normalizeEvent';
import { isNil } from '../utils/is';

import { isS3ChatEvent } from './isS3ChatEvent';

export const getEventData = async (
  eventData: ChatEventData | undefined,
): Promise<ChatEventData | null> => {
  if (isNil(eventData)) {
    return null;
  }

  if (!isS3ChatEvent(eventData)) {
    return eventData;
  }

  const url = eventData.data.s3Object.url;

  const response = await fetch(url);

  if (response.ok) {
    const responseData = await response.json();

    return normalizeEvent(responseData);
  }

  throw new ChatSDKError('Failed to fetch S3 event data');
};
