import { AwsResponseEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { ThreadRecoveredPostbackData } from '@brandembassy/ts-types-chat/dist/awsEvents/ThreadRecovered';

import { ChatEventData } from '../event/ChatEvent';
import { isUndefined } from '../utils/is';

export interface ThreadRecoveredChatEvent extends ChatEventData {
  data: ThreadRecoveredPostbackData;
}

export const isRecoverSuccessPayload = (
  response: ChatEventData,
): response is ThreadRecoveredChatEvent => {
  const testResponseData = response.data as ThreadRecoveredPostbackData;

  const hasPostbackData = isUndefined(testResponseData) === false;
  if (hasPostbackData === false) {
    return false;
  }

  const hasMessages = isUndefined(testResponseData?.messages) === false;
  if (hasMessages === false) {
    return false;
  }

  const isCorrectEventType =
    response.type === AwsResponseEventType.THREAD_RECOVERED ||
    response.type === AwsResponseEventType.LIVECHAT_RECOVERED;
  if (isCorrectEventType === false) {
    return false;
  }

  const hasNoError = isUndefined(response.error);
  if (hasNoError === false) {
    return false;
  }

  const hasThreadId = isUndefined(testResponseData?.thread) === false;
  if (hasThreadId === false) {
    return false;
  }

  return true;
};
