import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';

import { isUndefined } from '../utils/is';
import { SDKVariableStorage } from '../variables/VariableStorage';

import { fetchChannelInfo } from './fetchChannelInfo';

export async function handleBasicChannelInfo(
  isAuthorizationEnabled: undefined | boolean,
  isLivechat: undefined | boolean,
): Promise<{
  isAuthorizationEnabled: boolean;
  isLivechat: boolean;
}> {
  if (!isUndefined(isAuthorizationEnabled) && !isUndefined(isLivechat)) {
    return {
      isAuthorizationEnabled,
      isLivechat,
    };
  }

  const channelInfo = await fetchChannelInfo(
    SDKVariableStorage.BRAND_ID as BrandId,
    SDKVariableStorage.CHANNEL_ID as ChannelId,
  );

  return {
    isAuthorizationEnabled: channelInfo.isAuthorizationEnabled,
    isLivechat: channelInfo.isLiveChat,
  };
}
