import { createQueryParametersAsString } from '@brandembassy/be-javascript-utils/dist/query/queryParameters';
import {
  WebSocketClient as PushUpdateWSClient,
  WebSocketClientEvent as PushUpdatesWebSocketClientEvent,
} from '@brandembassy/push-updates-sdk';
import { setupSocketConnection } from '@brandembassy/push-updates-sdk/dist/pushUpdate/pushUpdateWebSocket';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';
import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/endUser/EndUserIdentityId';
import { VisitorId } from '@brandembassy/ts-types-platform/dist/visitor/VisitorId';

import { CHAT_SDK_VERSION } from '../env/version';
import { ChatCustomEvent } from '../event/ChatEvent';
import { SDKVariableStorage } from '../variables/VariableStorage';

import { WebSocketClientError } from './WebSocketClientError';
import { IWsEventTarget, WsEventTarget } from './WsEventTarget';

export interface SocketQueryParameters {
  [key: string]: string | number | undefined;
  brandId: BrandId;
  channelId: ChannelId;
  consumerId: CustomerIdentityIdOnExternalPlatform;
  token?: string;
  visitorId: VisitorId;
}

const MAX_SOCKET_RECONNECT_RETRIES = 20;

function createSocketUrl(
  host: string,
  prefix: string,
  additionalQueryParameters: SocketQueryParameters,
): string {
  const appVersion = SDKVariableStorage.APP_VERSION;
  const queryParameters = {
    v: appVersion,
    sdkPlatform: 'web',
    sdkVersion: CHAT_SDK_VERSION,
    ...additionalQueryParameters,
  };

  const queryParametersAsString =
    createQueryParametersAsString(queryParameters);

  return `${host}/${prefix}?${queryParametersAsString}`;
}

export interface WebSocketClientOptions {
  forceSecureProtocol?: boolean;
  host?: string;
  onError?: (error: WebSocketClientError) => void;
  port?: string;
  prefix?: string;
}

/**
 * Websocket client
 */
export class WebSocketClient {
  #connection: PushUpdateWSClient | null = null;
  #eventTarget: IWsEventTarget;
  #alreadyConnected: boolean = false;

  constructor(
    private brandId: BrandId,
    private channelId: ChannelId,
    private customerId: CustomerIdentityIdOnExternalPlatform,
    private options: WebSocketClientOptions,
    private onError: ((error: Error) => void) | undefined,
    private visitorId: VisitorId,
  ) {
    this.#eventTarget = new WsEventTarget();
  }

  /**
   * Connect websocket
   */
  async connect(token?: string): Promise<void> {
    this.#setupConnection(token);

    this.#connection?.socket?.reconnect();
  }

  /**
   * Disconnect websocket
   */
  disconnect(): void {
    this.#connection?.socket.close();
  }

  /**
   * Reconnect websocket
   */
  reconnect(token?: string): void {
    this.#setupConnection(token);
    this.#connection?.socket.reconnect();
  }

  /**
   * Send data to active connection
   * @param data - data to send
   */
  send(data: unknown): void {
    const stringData = JSON.stringify(data);
    this.#connection?.send(stringData);
  }

  /**
   * Register event handler to websocket event
   * @param eventType - websocket event
   * @param handlerCallback - event handler
   */
  on(
    eventType: PushUpdatesWebSocketClientEvent,
    handlerCallback: (event: ChatCustomEvent) => void,
  ): void {
    this.#eventTarget.addEventListener(eventType, handlerCallback);
  }

  /**
   * Unregister event handler to websocket event
   * @param eventType - websocket event
   * @param handlerCallback - event handler
   */
  off(
    eventType: PushUpdatesWebSocketClientEvent,
    handlerCallback: (event: CustomEvent) => void,
  ): void {
    this.#eventTarget.removeEventListener(eventType, handlerCallback);
  }

  /**
   * Handle error from event listeners with onError callback or throw error
   */
  private _errorHandler(
    event: CustomEvent<CloseEvent | ErrorEvent | Event>,
  ): void {
    const eventError = event.detail;
    let error: WebSocketClientError | undefined;

    if (eventError instanceof ErrorEvent) {
      error = new WebSocketClientError('Connection error', eventError.message);
    }
    if (eventError instanceof CloseEvent) {
      error = new WebSocketClientError('Connection closed', eventError.reason);
    }
    if (typeof error === 'undefined') {
      error = new WebSocketClientError('Unknown error', eventError.type);
    }

    if (typeof this.onError === 'function') {
      this.onError(error);
    } else {
      throw error;
    }
  }

  #setupConnection(token?: string): void {
    const port = this.options?.port ? `:${this.options?.port}` : '';
    const host = this.options?.host ? `${this.options?.host}${port}` : '';
    const prefix = this.options?.prefix ?? '';
    const forceSecureProtocol = this.options?.forceSecureProtocol ?? true;

    const socketUrl = createSocketUrl(host, prefix, {
      brandId: this.brandId,
      channelId: this.channelId,
      consumerId: this.customerId,
      visitorId: this.visitorId,
      ...(token ? { transactionToken: token } : {}),
    });

    const heartbeatAfterAuthorize = !token;

    this.#connection = setupSocketConnection(socketUrl, {
      startClosed: true,
      forceSecureProtocol,
      heartbeatAfterAuthorize,
      maxRetries: 0, // Disable reconnection attempts on the initial connection to avoid retrying in case of an authorization error. Configure maxRetries later once the connection is successfully established.
    });

    const errorHandlerBound = this._errorHandler.bind(this);
    this.#connection?.addEventListener(
      PushUpdatesWebSocketClientEvent.CLOSE,
      errorHandlerBound,
    );
    this.#connection?.addEventListener(
      PushUpdatesWebSocketClientEvent.ERROR,
      errorHandlerBound,
    );

    this.#connection?.addEventListener(
      PushUpdatesWebSocketClientEvent.OPEN,
      (event: CustomEvent<Event>) => {
        this.#connection?.socket.setMaxRetires(MAX_SOCKET_RECONNECT_RETRIES);
        this.#alreadyConnected = true;
        this.#socketEventHandler(event);
      },
    );
    this.#connection?.addEventListener(
      PushUpdatesWebSocketClientEvent.CLOSE,
      (event: CustomEvent<CloseEvent>) => {
        if (!this.#alreadyConnected) {
          // if the connection was not establish before close event.
          // It is most probably because of the Authorization failed
          this.#eventTarget.dispatchEvent(
            new CustomEvent(
              PushUpdatesWebSocketClientEvent.AUTHORIZATION_FAILED,
            ),
          );
        }

        this.#socketEventHandler(event);
      },
    );
    this.#connection?.addEventListener(
      PushUpdatesWebSocketClientEvent.MESSAGE,
      this.#socketEventHandler,
    );
  }

  #socketEventHandler = (
    event: CustomEvent<CloseEvent | ErrorEvent | Event>,
  ): void => {
    this.#eventTarget.dispatchEvent(event);
  };
}
