import { PushUpdateEventFields } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateEventFields';

import { ChatEventData } from '../event/ChatEvent';
import { normalizeEvent } from '../event/normalizeEvent';

export function threadRecovered(event: ChatEventData): ChatEventData {
  const normalizedContactHistoryItems = (
    (event.data as Record<string, unknown>)
      .contactHistory as Array<PushUpdateEventFields>
  ).map(normalizeEvent);

  return {
    ...event,
    data: {
      ...(event.data as Record<string, unknown>),
      contactHistory: normalizedContactHistoryItems,
    },
  };
}
